export default {
	props: {
		mode: {
			value: Number,
			default: 1
		},
		//HM修改 定义默认搜索关键词(水印文字)
		placeholder:{
			value: String,
			default: '请输入您要搜索的信息'
		},
		value: {
			type: String,
			default: 'false'
		},
		button: {
			value: String,
			default: 'outside'
		},
		show: {
			value: Boolean,
			default: true
		},
		radius: {
			value: String,
			default: 60
		}
	},
	data() {
		return {
			active: false,
			inputVal: '',
			searchName: '取消',
			isDelShow: false,
			isFocus: true
		};
	},
	methods: {
		//HM修改 触发组件confirm事件
		triggerConfirm(){
			this.$emit('confirm', false);
		},
		//HM修改 触发组件input事件
		inputChange(event){
			var keyword = event.detail.value;
			this.$emit('input', keyword);
			if (this.inputVal) {
				this.isDelShow = true;
			}
		},
		focus() {
			this.active = true;
			this.$emit('getFocus',true)
			//HM修改 增加获取焦点判断
			if (this.inputVal) {
				this.isDelShow = true;
			}
		},
		blur() {
			this.isFocus = true;
			if (!this.inputVal) {
				// this.active = false;
				// this.$emit('getFocus',false)
			}
		},
		//清空搜索
		clearInput() {
			//HM修改 收起键盘
			this.inputVal = '';
			//this.$emit('search', '');//HM修改 清空内容时候不进行搜索
		},
		//退出搜索界面
		clearButton() {
			//HM修改 收起键盘
			console.log("hello world")
			uni.hideKeyboard();
			// this.isFocus = false;
			this.inputVal = '';
			// this.active = false;
			// this.$emit('getFocus',false)
		},
		getFocus() {
			this.isFocus = true;
		},
		doSearch() {
			//HM修改 增加点击取消时候退出输入状态，内容为空时，输入默认关键字
			if (!this.inputVal) {
				if(!this.show&&this.searchName == '取消'){
					uni.hideKeyboard();
					this.isFocus = false;
					this.active = false;
					return;
				}
			}
			console.log(this.inputVal); 
			uni.hideKeyboard();
			this.isFocus = false;
			this.active = false;
			//清空输入数据
			console.log(this.inputVal)
			this.$emit('search', this.inputVal?this.inputVal:this.placeholder);

		}
	},
	watch: {
		inputVal(newVal) {
			if (newVal) {
				this.searchName = '搜索';
				//this.isDelShow = true; //HM修改 直接点击页面预设关键字样式异常，注销
			} else {
				this.searchName = '取消';
				this.isDelShow = false;
			}
		},
		//HM修改 双向绑定
		value(val) {
			this.inputVal = val;
		},
	}
};
